<div class="main__banks-item"><div class="main__banks-item-up"><img :src="require(`@/assets/images/main/${item.icon}`)"><div class="main__banks-item-info"><p class="main__banks-item-name">{{item.name}}</p><p class="main__banks-item-license">{{item.license}}</p></div></div><div class="main__banks-item-bottom"><ul class="main__banks-item-list"><li class="main__banks-item-loc">{{item.location}}</li><li class="main__banks-item-text">{{item.phoneNumber}}</li><li class="main__banks-item-text">{{item.website}}</li></ul><a class="main__banks-item-btn" href="#!">Узнать больше</a></div></div>